.headerLogo {

    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 20% 80%;
    margin: 0;
    padding: 0 1.2rem;
    align-content: center;
}
.headerLogo_image {

    width: 1.9rem;
    justify-self: end;

}
.headerLogo_title {

    padding-left: .2rem;
    font-family: 'Berlin Sans FB';
    font-size: 1.5rem;
    color: var(--skyBlue);

}