.problemBox {

    width: 100vw;
    padding: 1rem 1.2rem;
    position: relative;

}
.problemBox_backgroundProblemBoxImageContainer {

    position: absolute;
    z-index: -100;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}
._backgroundProblemBoxImageContainer_image {

    height: 110%;

}
.problemBox_problemBoxTitleContainer {

    display: flex;
    align-items: center;
    width: 100%;
    height: 2rem;

}
._problemBoxTitleContainer_title {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    font-weight: bold;
    margin: 0;
    color: white;

}
.problemBox_commonText {

    font-size: 1.1rem;
    font-family: Arial, Helvetica, sans-serif;
    color: white;

}
.problemBox_overline {

    text-decoration: overline;

}