.coursesTableContainer {

    background-color: var(--black);
    width: 100vw;
    height: 90vh;
    position: fixed;
    z-index: 100;
    transform: translate(10vh, -100%);
    display: grid;
    grid-template-rows: 10vh 80vh;
    padding: 0 1.2rem;
    overflow-y: auto;

}
.appearCoursesTableContainer {

    transform: translateY(10vh);

}
.coursesTableContainer_coursesTable {

    display: grid;
    align-content: space-between;
    padding: 1.5rem 0;

}