@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.headerLogo {

    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 20% 80%;
    margin: 0;
    padding: 0 1.2rem;
    align-content: center;
}
.headerLogo_image {

    width: 1.9rem;
    justify-self: end;

}
.headerLogo_title {

    padding-left: .2rem;
    font-family: 'Berlin Sans FB';
    font-size: 1.5rem;
    color: var(--skyBlue);

}
.menuButton {

    background-color: transparent;
    border: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    padding: 0 1.2rem;
    justify-self: end;
    color: var(--skyBlue);
    font-size: 1rem;

}
.menuButton_image {
    width: 1.5rem;
}
.searchContainer {

    display: flex;
    justify-content: flex-start;
    align-items: center;

}
.searchContainer_imageSearchContainer {

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0;
    margin-right: 1rem;

}
._imageSearchContainer_imageSearch {

    width: 1.6rem;

}
.searchContainer_searchInput {

    width: 100%;
    height: 2.2rem;
    background-color: transparent;
    color: white;
    border: none;
    outline: none;
    font-size: 1rem;

}
.searchContainer_searchInput:focus {

    background-color: rgba(255 255 255 /.08);

}
.session {

    width: 100%;
    height: 3rem;
    display: flex;
    align-items: center;

}

.session_title {

    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    color: white;

}
.courseContainer {

    width: 100%;
    height: 100%;

}
.courseContainer_course {

    display: flex;
    align-items: center;

}
._course_title {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.2rem;
    color: var(--skyBlue);

}
._course_chevron {

    width: 1.5rem;
    margin-left: .5rem;

}
.courseContainer_closedSessionsContainer {

    height: 0;
    overflow: hidden;

}
.courseContainer_openSessionsContainer {

    display: grid;
    align-content: space-between;
    padding-left: 1em;

}
.coursesTableContainer {

    background-color: var(--black);
    width: 100vw;
    height: 90vh;
    position: fixed;
    z-index: 100;
    -webkit-transform: translate(10vh, -100%);
            transform: translate(10vh, -100%);
    display: grid;
    grid-template-rows: 10vh 80vh;
    padding: 0 1.2rem;
    overflow-y: auto;

}
.appearCoursesTableContainer {

    -webkit-transform: translateY(10vh);

            transform: translateY(10vh);

}
.coursesTableContainer_coursesTable {

    display: grid;
    align-content: space-between;
    padding: 1.5rem 0;

}
.navigator {

    width: 100vw;
    height: 10vh;
    display: grid;
    grid-template-columns: 60% 40%;
    background-color: var(--black);

}

.fixNavigator {

    position: fixed;
    top: 0;
    z-index: 100;

}
.listBox {

    width: 100vw;
    padding: 1rem 1.2rem;
    position: relative;

}
.listBox_backgroundListBoxImageContainer {

    position: absolute;
    z-index: -100;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}
._backgroundListBoxImageContainer_image {

    height: 100%;

}
.listBox_listBoxTitleContainer {

    display: flex;
    align-items: center;
    width: 100%;
    height: 2rem;

}
._listBoxTitleContainer_title {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    font-weight: bold;
    margin: 0;
    color: white;

}
.listBox_list {

    list-style: inside;
    -webkit-padding-start: 1rem;
            padding-inline-start: 1rem;
    display: grid;
    grid-row-gap: 1rem;

}
._list_item {

    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    color: white;

}
.problemBox {

    width: 100vw;
    padding: 1rem 1.2rem;
    position: relative;

}
.problemBox_backgroundProblemBoxImageContainer {

    position: absolute;
    z-index: -100;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}
._backgroundProblemBoxImageContainer_image {

    height: 110%;

}
.problemBox_problemBoxTitleContainer {

    display: flex;
    align-items: center;
    width: 100%;
    height: 2rem;

}
._problemBoxTitleContainer_title {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    font-weight: bold;
    margin: 0;
    color: white;

}
.problemBox_commonText {

    font-size: 1.1rem;
    font-family: Arial, Helvetica, sans-serif;
    color: white;

}
.problemBox_overline {

    text-decoration: overline;

}
.class {

    width: 100%;
    padding: 1.2rem 1rem;
    background-color: var(--black);

}
.class_classNameContainer{

    width: 100%;
    display: flex;
    justify-content: center;

}
._classNameContainer_className {

    font-size: 1.5rem;
    font-family: Roboto;
    color: white;

}
.class_mainClassImageContainer {

    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;

}
._mainClassImageContainer_mainClassImage {

    width: 95%

}
.class_subTopic {

    margin: 1em 0;

}
._subTopicTitleContainer_subTopicTitle1 {

    font-family: Helvetica;
    font-size: 1.2rem;
    color: white;

}
._subTopicTitleContainer_subTopicTitle2 {

    font-family: Helvetica;
    font-size: 1rem;
    color: white;

}
.class_paragraph {

    font-family: Roboto;
    font-weight: thin;
    font-size: 1.05rem;
    color: white;

}
.class_classList {

    padding: 0;
    list-style: inside;

}
._classList_item {

    font-family: Helvetica;
    font-size: 1.1rem;
    color: white;

}
.class_classAnimation {

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 6rem;
    position: relative;
    margin: 2rem 0;

}
._classAnimation_machine {

    background-color: var(--skyBlue);
    width: 6rem;
    height: 6rem;

}
._classAnimation_number {

    position: absolute;
    left: 1rem;
    font-size: 2rem;
    color: white;

}
.iconBox {



}
.iconBox_image {

    width: 4rem;    

}
.footer {

    width: 100%;
    background-color: var(--black);
    padding: 1rem;

}
.footer_footerTitleContainer {

    padding-bottom: 1rem;

}
._footerTitleContainer_title {

    color: var(--skyBlue);
    margin: 0;
    font-size: 1.3rem;
    font-family: Arial, Helvetica, sans-serif

}
.footer_iconsContainer {

    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}
