@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.class {

    width: 100%;
    padding: 1.2rem 1rem;
    background-color: var(--black);

}
.class_classNameContainer{

    width: 100%;
    display: flex;
    justify-content: center;

}
._classNameContainer_className {

    font-size: 1.5rem;
    font-family: Roboto;
    color: white;

}
.class_mainClassImageContainer {

    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;

}
._mainClassImageContainer_mainClassImage {

    width: 95%

}
.class_subTopic {

    margin: 1em 0;

}
._subTopicTitleContainer_subTopicTitle1 {

    font-family: Helvetica;
    font-size: 1.2rem;
    color: white;

}
._subTopicTitleContainer_subTopicTitle2 {

    font-family: Helvetica;
    font-size: 1rem;
    color: white;

}
.class_paragraph {

    font-family: Roboto;
    font-weight: thin;
    font-size: 1.05rem;
    color: white;

}
.class_classList {

    padding: 0;
    list-style: inside;

}
._classList_item {

    font-family: Helvetica;
    font-size: 1.1rem;
    color: white;

}
.class_classAnimation {

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 6rem;
    position: relative;
    margin: 2rem 0;

}
._classAnimation_machine {

    background-color: var(--skyBlue);
    width: 6rem;
    height: 6rem;

}
._classAnimation_number {

    position: absolute;
    left: 1rem;
    font-size: 2rem;
    color: white;

}