.courseContainer {

    width: 100%;
    height: 100%;

}
.courseContainer_course {

    display: flex;
    align-items: center;

}
._course_title {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.2rem;
    color: var(--skyBlue);

}
._course_chevron {

    width: 1.5rem;
    margin-left: .5rem;

}
.courseContainer_closedSessionsContainer {

    height: 0;
    overflow: hidden;

}
.courseContainer_openSessionsContainer {

    display: grid;
    align-content: space-between;
    padding-left: 1em;

}