.searchContainer {

    display: flex;
    justify-content: flex-start;
    align-items: center;

}
.searchContainer_imageSearchContainer {

    width: fit-content;
    height: fit-content;
    margin: 0;
    margin-right: 1rem;

}
._imageSearchContainer_imageSearch {

    width: 1.6rem;

}
.searchContainer_searchInput {

    width: 100%;
    height: 2.2rem;
    background-color: transparent;
    color: white;
    border: none;
    outline: none;
    font-size: 1rem;

}
.searchContainer_searchInput:focus {

    background-color: rgba(255 255 255 /.08);

}