.listBox {

    width: 100vw;
    padding: 1rem 1.2rem;
    position: relative;

}
.listBox_backgroundListBoxImageContainer {

    position: absolute;
    z-index: -100;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}
._backgroundListBoxImageContainer_image {

    height: 100%;

}
.listBox_listBoxTitleContainer {

    display: flex;
    align-items: center;
    width: 100%;
    height: 2rem;

}
._listBoxTitleContainer_title {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    font-weight: bold;
    margin: 0;
    color: white;

}
.listBox_list {

    list-style: inside;
    padding-inline-start: 1rem;
    display: grid;
    grid-row-gap: 1rem;

}
._list_item {

    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    color: white;

}